import * as React from "react";
import Masonry from "react-masonry-css";
import Fade from "react-reveal/Fade";
import { Modal, Tabs } from "antd";
import { useState } from "react";
import styles from "./styles.module.scss";
import Title from "../../components/Title";
import Section from "../../components/Section";
import Item from "./Item";
import Button from "../../components/Button";
import { projects, moreProjects } from "./constants";

const { TabPane } = Tabs;

const Project = () => {
  const [moreVisible, setMoreVisible] = useState<boolean>(false);

  return (
    <Section id="project" className={styles.project}>
      <Title tag="h2">User Journey</Title>

      <Masonry
        breakpointCols={{ default: 2, 992: 1, 786: 1 }}
        className={styles.projectList}
        columnClassName={styles.projectListColumn}
      >
        {projects.map((project) => (
          <Item {...project} key={project.logo} />
        ))}
        <Fade>
          {/*<div className={styles.more}>*/}
          {/*  <Button*/}
          {/*    className={styles.moreBtn}*/}
          {/*    onClick={() => setMoreVisible(true)}*/}
          {/*    bg="#15a000"*/}
          {/*  >*/}
          {/*    更多*/}
          {/*  </Button>*/}
          {/*</div>*/}
        </Fade>
      </Masonry>

      <Modal
        width="100%"
        title="更多项目"
        visible={moreVisible}
        footer={null}
        onCancel={() => setMoreVisible(false)}
      >
        <Tabs>
          {moreProjects.map((project) => (
            <TabPane key={project.title} tab={project.title}>
              <iframe
                title={project.title}
                style={{ width: "100%", height: "50vh" }}
                src={project.url}
                frameBorder="0"
              />
            </TabPane>
          ))}
        </Tabs>
      </Modal>
    </Section>
  );
};

export default Project;
